'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import Image from 'next/image'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Badge } from '@/components/ui/badge'
import { Separator } from '@/components/ui/separator'

export default function InspectorDashboard() {
  const [user, setUser] = useState<any>(null)
  const [pendingCalls, setPendingCalls] = useState<any[]>([])
  const [acceptedCall, setAcceptedCall] = useState<any>(null)
  const [loading, setLoading] = useState<string>('')
  const [notificationPermission, setNotificationPermission] = useState<string>('')
  const router = useRouter()

  useEffect(() => {
    // Check authentication
    const currentUser = localStorage.getItem('currentUser')
    if (!currentUser) {
      router.push('/')
      return
    }

    const userData = JSON.parse(currentUser)
    if (userData.role !== 'inspector') {
      router.push('/')
      return
    }

    setUser(userData)
    
    // Request notification permission
    if ('Notification' in window) {
      if (Notification.permission === 'default') {
        Notification.requestPermission().then(permission => {
          setNotificationPermission(permission)
        })
      } else {
        setNotificationPermission(Notification.permission)
      }
    }

    loadCalls(userData.id)
  }, [router])

  useEffect(() => {
    // Poll for new calls every 2 seconds
    const interval = setInterval(() => {
      if (user) {
        loadCalls(user.id)
      }
    }, 2000)

    return () => clearInterval(interval)
  }, [user])

  const loadCalls = (inspectorId: string) => {
    const calls = JSON.parse(localStorage.getItem('emergencyCalls') || '[]')
    
    // Get pending calls (not accepted by anyone)
    const pending = calls.filter((call: any) => call.status === 'pending')
    
    // Get calls accepted by this inspector
    const accepted = calls.find((call: any) => 
      call.status === 'accepted' && call.assignedInspector === inspectorId
    )

    setPendingCalls(pending)
    setAcceptedCall(accepted)

    // Show browser notification for new pending calls
    if (pending.length > 0 && notificationPermission === 'granted') {
      const latestCall = pending[pending.length - 1]
      const callTime = new Date(latestCall.createdAt).getTime()
      const now = new Date().getTime()
      
      // Only notify if call is less than 5 seconds old (newly created)
      if (now - callTime < 5000) {
        new Notification('🚨 Nueva Emergencia - Liceo Pullinque', {
          body: `${latestCall.teacherName} necesita ayuda en ${latestCall.location}`,
          icon: '/liceo-logo.svg',
          requireInteraction: true
        })
      }
    }
  }

  const handleAcceptCall = async (callId: string) => {
    setLoading(callId)

    try {
      const calls = JSON.parse(localStorage.getItem('emergencyCalls') || '[]')
      const updatedCalls = calls.map((call: any) => {
        if (call.id === callId) {
          return {
            ...call,
            status: 'accepted',
            assignedInspector: user.name,
            acceptedAt: new Date().toISOString()
          }
        }
        return call
      })

      localStorage.setItem('emergencyCalls', JSON.stringify(updatedCalls))
      loadCalls(user.id)

    } catch (err) {
      console.error('Error accepting call:', err)
    } finally {
      setLoading('')
    }
  }

  const handleCompleteCall = async () => {
    if (!acceptedCall) return

    try {
      const calls = JSON.parse(localStorage.getItem('emergencyCalls') || '[]')
      const updatedCalls = calls.map((call: any) => {
        if (call.id === acceptedCall.id) {
          return {
            ...call,
            status: 'completed',
            completedAt: new Date().toISOString()
          }
        }
        return call
      })

      localStorage.setItem('emergencyCalls', JSON.stringify(updatedCalls))
      setAcceptedCall(null)
      loadCalls(user.id)

    } catch (err) {
      console.error('Error completing call:', err)
    }
  }

  const handleLogout = () => {
    localStorage.removeItem('currentUser')
    router.push('/')
  }

  const formatTimeAgo = (dateString: string) => {
    const now = new Date().getTime()
    const callTime = new Date(dateString).getTime()
    const diffMinutes = Math.floor((now - callTime) / (1000 * 60))
    
    if (diffMinutes < 1) return 'Hace menos de 1 minuto'
    if (diffMinutes === 1) return 'Hace 1 minuto'
    return `Hace ${diffMinutes} minutos`
  }

  if (!user) {
    return <div className="min-h-screen flex items-center justify-center">Cargando...</div>
  }

  return (
    <div className="min-h-screen p-4">
      {/* Header */}
      <div className="max-w-4xl mx-auto mb-6">
        <div className="flex items-center justify-between bg-white rounded-lg shadow-sm p-4">
          <div className="flex items-center space-x-4">
            <Image
              src="/liceo-logo.svg"
              alt="Liceo Pullinque"
              width={40}
              height={48}
              className="h-12 w-auto"
            />
            <div>
              <h1 className="text-xl font-bold text-blue-900">Panel de Inspector</h1>
              <p className="text-blue-700">Bienvenido, {user.name}</p>
              <p className="text-sm text-gray-600">Área: {user.area}</p>
            </div>
          </div>
          <Button 
            onClick={handleLogout}
            variant="outline"
            className="border-blue-200 text-blue-700 hover:bg-blue-50"
          >
            Cerrar Sesión
          </Button>
        </div>
      </div>

      <div className="max-w-4xl mx-auto space-y-6">
        {/* Notification Permission Alert */}
        {notificationPermission !== 'granted' && (
          <Alert className="border-yellow-200 bg-yellow-50">
            <AlertDescription className="text-yellow-700">
              Para recibir notificaciones de emergencia, por favor permite las notificaciones del navegador.
            </AlertDescription>
          </Alert>
        )}

        {/* Accepted Call */}
        {acceptedCall && (
          <Card className="border-l-4 border-l-green-500">
            <CardHeader>
              <CardTitle className="flex items-center justify-between">
                <span>Emergencia Asignada</span>
                <Badge className="bg-green-100 text-green-800">En Progreso</Badge>
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-3">
                <div>
                  <p><strong>Profesor:</strong> {acceptedCall.teacherName}</p>
                  <p><strong>Ubicación:</strong> {acceptedCall.location}</p>
                  <p><strong>Descripción:</strong> {acceptedCall.description}</p>
                  <p><strong>Hora de llamada:</strong> {new Date(acceptedCall.createdAt).toLocaleString('es-CL')}</p>
                  <p><strong>Aceptada:</strong> {formatTimeAgo(acceptedCall.acceptedAt)}</p>
                </div>
                <Separator />
                <div className="flex space-x-3">
                  <Button 
                    onClick={handleCompleteCall}
                    className="bg-green-600 hover:bg-green-700"
                  >
                    Marcar como Completada
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Pending Calls */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center justify-between">
              <span>Llamadas de Emergencia Pendientes</span>
              <Badge variant="outline">
                {pendingCalls.length} {pendingCalls.length === 1 ? 'llamada' : 'llamadas'}
              </Badge>
            </CardTitle>
            <CardDescription>
              Estas son las emergencias que necesitan atención inmediata
            </CardDescription>
          </CardHeader>
          <CardContent>
            {pendingCalls.length === 0 ? (
              <div className="text-center py-8 text-gray-500">
                <p>No hay emergencias pendientes</p>
                <p className="text-sm mt-2">Las nuevas llamadas aparecerán aquí automáticamente</p>
              </div>
            ) : (
              <div className="space-y-4">
                {pendingCalls.map((call) => (
                  <div key={call.id} className="border rounded-lg p-4 bg-red-50 border-red-200">
                    <div className="flex items-start justify-between">
                      <div className="flex-1">
                        <div className="flex items-center space-x-2 mb-2">
                          <Badge className="bg-red-100 text-red-800">URGENTE</Badge>
                          <span className="text-sm text-gray-600">
                            {formatTimeAgo(call.createdAt)}
                          </span>
                        </div>
                        <h3 className="font-semibold text-lg">{call.teacherName}</h3>
                        <p className="text-gray-700 mb-1">
                          <strong>Ubicación:</strong> {call.location}
                        </p>
                        <p className="text-gray-700 mb-3">
                          <strong>Descripción:</strong> {call.description}
                        </p>
                        <p className="text-sm text-gray-600">
                          Llamada realizada: {new Date(call.createdAt).toLocaleString('es-CL')}
                        </p>
                      </div>
                      <div className="ml-4">
                        <Button 
                          onClick={() => handleAcceptCall(call.id)}
                          disabled={loading === call.id || acceptedCall !== null}
                          className="bg-green-600 hover:bg-green-700 font-semibold"
                        >
                          {loading === call.id ? 'Aceptando...' : 'Aceptar'}
                        </Button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Instructions */}
        <Card>
          <CardHeader>
            <CardTitle>Instrucciones para Inspectores</CardTitle>
          </CardHeader>
          <CardContent>
            <ul className="space-y-2 text-sm text-gray-600">
              <li>• Las nuevas emergencias aparecerán automáticamente en esta pantalla</li>
              <li>• Recibirás notificaciones del navegador cuando haya nuevas llamadas</li>
              <li>• Solo puedes aceptar una emergencia a la vez</li>
              <li>• Una vez que aceptes una llamada, desaparecerá para otros inspectores</li>
              <li>• Dirígete inmediatamente a la ubicación indicada</li>
              <li>• Marca la emergencia como completada una vez resuelta la situación</li>
            </ul>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
